@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (min-width: 599px) {
  .avatar2 {
    display: none;
  }
}

html {
  scroll-behavior: smooth;
}

.home {
  font-family: "Poppins", sans-serif;
  margin-top: 20px;
  background: url(/portfl/static/media/wall.b230b7e6.svg);
  margin-top: -1px;
  height: 100vh;
  background-position: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  height: 85vh;
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
}

.site-links {
  text-decoration: none !important;
  color: white !important;
  padding: 5px !important;
}

.site-links:hover {
  color: black !important;
}

.homepage-wall {
  display: flex;
  align-items: center;
  justify-content: center;
}

.homepage-wall-header {
  display: flex;
  align-items: center;
  height: 85vh;
}

.homepage-wall-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 85vh;
}

.homepage-header-head {
  font-size: 50px;
}

.avatar {
  box-shadow: 5px 0px 70px 10px rgba(0, 0, 0, 0.5);
  border-radius: 100px;
  height: 200px;
  width: 200px;
}

.project-card {
  padding: 15px;
}

.cards {
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1), 0 4px 4px 0 rgba(0, 0, 0, 0.15),
    0 2px 2px 0 rgba(0, 0, 0, 0.3) !important;
}

.projects-header {
  font-family: "Poppins", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  color: white;
  font-weight: 600;
  padding: 5px;
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
}

.page {
  background: url(/portfl/static/media/wall.341d5da4.png);
  background-attachment: fixed;
}

.project-items {
  display: flex;
  align-items: center;
  justify-content: center;
}

.myprojects {
  background: rgba(225, 225, 225, 0.7);
  padding-top: 70px;
}

.works {
  background: rgb(101, 94, 241);
  background: -webkit-gradient(
    linear,
    left top, right top,
    color-stop(39%, rgba(101, 94, 241, 0.9)),
    color-stop(98%, rgba(56, 221, 219, 0.9)),
    to(rgba(29, 32, 33, 0.9))
  );
  background: linear-gradient(
    90deg,
    rgba(101, 94, 241, 0.9) 39%,
    rgba(56, 221, 219, 0.9) 98%,
    rgba(29, 32, 33, 0.9) 100%
  );
  padding: 20px;
}

.works-header {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  color: white;
  font-weight: 600;
  padding: 5px;
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
}

.works-items {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about {
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: rgba(255, 255, 255, 0.9);
}

.about-images {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-image {
  border-radius: 20px;
  box-shadow: 5px 0px 70px 10px rgba(0, 0, 0, 0.5);
}

.about-info {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.contact {
  background: black;
  color: white;
  padding: 20px;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(20, 20, 20);
  color: white;
  left: 0;
  right: 0;
  bottom: 0;
}

.link-dialog {
  text-decoration: none;
  color: black !important;
}

.link-dialog :hover {
  color: blue;
}

@media screen and (max-width: 599px) {
  .avatar {
    display: none;
  }

  .avatar2 {
    border-radius: 50px;
    margin-top: 80px;
    height: 90px;
    width: 90px;
  }
}

.navbar {
  height: 70px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1);
  width: 100%;
  position: fixed;
  top: 0;
}


.navbar-nav {
  display: flex;
  height: 100%;
  padding: 0 1rem;
  align-items: center;
}

.spacer {
  flex: 1 1;
}

.navbar-logo {
  margin-left: 30px;
}

.navbar-links a {
  text-decoration: none;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
}

.navbar-links li:hover,
li:active {
  background: rgb(101, 94, 241);
  background: -webkit-gradient(linear,
      left top, right top,
      color-stop(39%, rgba(101, 94, 241, 0.9)),
      color-stop(98%, rgba(56, 221, 219, 0.9)),
      to(rgba(29, 32, 33, 0.9)));
  background: linear-gradient(90deg,
      rgba(101, 94, 241, 0.9) 39%,
      rgba(56, 221, 219, 0.9) 98%,
      rgba(29, 32, 33, 0.9) 100%);
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: 20px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1), 0 4px 4px 0 rgba(0, 0, 0, 0.15),
    0 2px 2px 0 rgba(0, 0, 0, 0.6);
}

.navbar-links a:hover,
a:active {
  color: white;
}

.navbar-links ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}


@media screen and (max-width:784px) {
  .navbar-links {
    display: none;
  }
}
@media screen and (min-width:784px) {
    .toggle-button {
        display: none;
    }
}

@media screen and (max-width:784px) {
    .toggle-button {
        height: 30px;
        width: 36px;
        border: none;
        cursor: pointer;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        background: transparent;
    }

    .toggle-button:focus {
        outline: none;
    }
}
.drawer-links {
    text-decoration: none !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    color: black !important;
}

.drawer-links:hover {
    outline: none;
}

.drawer-list-items {
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
}


.drawer-link-text {
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
}

.drawer-list-items :hover {
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    width: 100%;
    background: rgb(101, 94, 241);
    background: -webkit-gradient(linear,
            left top, right top,
            color-stop(39%, rgba(101, 94, 241, 0.9)),
            color-stop(98%, rgba(56, 221, 219, 0.9)),
            to(rgba(29, 32, 33, 0.9)));
    background: linear-gradient(90deg,
            rgba(101, 94, 241, 0.9) 39%,
            rgba(56, 221, 219, 0.9) 98%,
            rgba(29, 32, 33, 0.9) 100%);

    border-radius: 10px;
    box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1);
    color: white !important;
}
