@media screen and (min-width:784px) {
    .toggle-button {
        display: none;
    }
}

@media screen and (max-width:784px) {
    .toggle-button {
        height: 30px;
        width: 36px;
        border: none;
        cursor: pointer;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        background: transparent;
    }

    .toggle-button:focus {
        outline: none;
    }
}