@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900&display=swap");

@media screen and (min-width: 599px) {
  .avatar2 {
    display: none;
  }
}

html {
  scroll-behavior: smooth;
}

.home {
  font-family: "Poppins", sans-serif;
  margin-top: 20px;
  background: url("../images/wall.svg");
  margin-top: -1px;
  height: 100vh;
  background-position: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  height: 85vh;
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
}

.site-links {
  text-decoration: none !important;
  color: white !important;
  padding: 5px !important;
}

.site-links:hover {
  color: black !important;
}

.homepage-wall {
  display: flex;
  align-items: center;
  justify-content: center;
}

.homepage-wall-header {
  display: flex;
  align-items: center;
  height: 85vh;
}

.homepage-wall-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 85vh;
}

.homepage-header-head {
  font-size: 50px;
}

.avatar {
  box-shadow: 5px 0px 70px 10px rgba(0, 0, 0, 0.5);
  border-radius: 100px;
  height: 200px;
  width: 200px;
}

.project-card {
  padding: 15px;
}

.cards {
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1), 0 4px 4px 0 rgba(0, 0, 0, 0.15),
    0 2px 2px 0 rgba(0, 0, 0, 0.3) !important;
}

.projects-header {
  font-family: "Poppins", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  color: white;
  font-weight: 600;
  padding: 5px;
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
}

.page {
  background: url("../images/wall.png");
  background-attachment: fixed;
}

.project-items {
  display: flex;
  align-items: center;
  justify-content: center;
}

.myprojects {
  background: rgba(225, 225, 225, 0.7);
  padding-top: 70px;
}

.works {
  background: rgb(101, 94, 241);
  background: linear-gradient(
    90deg,
    rgba(101, 94, 241, 0.9) 39%,
    rgba(56, 221, 219, 0.9) 98%,
    rgba(29, 32, 33, 0.9) 100%
  );
  padding: 20px;
}

.works-header {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  color: white;
  font-weight: 600;
  padding: 5px;
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
}

.works-items {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about {
  text-shadow: 6px 6px 12px rgba(0, 0, 0, 0.5),
    -6px -6px 12px rgba(255, 255, 255, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: rgba(255, 255, 255, 0.9);
}

.about-images {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-image {
  border-radius: 20px;
  box-shadow: 5px 0px 70px 10px rgba(0, 0, 0, 0.5);
}

.about-info {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.contact {
  background: black;
  color: white;
  padding: 20px;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(20, 20, 20);
  color: white;
  left: 0;
  right: 0;
  bottom: 0;
}

.link-dialog {
  text-decoration: none;
  color: black !important;
}

.link-dialog :hover {
  color: blue;
}

@media screen and (max-width: 599px) {
  .avatar {
    display: none;
  }

  .avatar2 {
    border-radius: 50px;
    margin-top: 80px;
    height: 90px;
    width: 90px;
  }
}
