.navbar {
  height: 70px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1);
  width: 100%;
  position: fixed;
  top: 0;
}


.navbar-nav {
  display: flex;
  height: 100%;
  padding: 0 1rem;
  align-items: center;
}

.spacer {
  flex: 1;
}

.navbar-logo {
  margin-left: 30px;
}

.navbar-links a {
  text-decoration: none;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
}

.navbar-links li:hover,
li:active {
  background: rgb(101, 94, 241);
  background: linear-gradient(90deg,
      rgba(101, 94, 241, 0.9) 39%,
      rgba(56, 221, 219, 0.9) 98%,
      rgba(29, 32, 33, 0.9) 100%);
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: 20px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1), 0 4px 4px 0 rgba(0, 0, 0, 0.15),
    0 2px 2px 0 rgba(0, 0, 0, 0.6);
}

.navbar-links a:hover,
a:active {
  color: white;
}

.navbar-links ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}


@media screen and (max-width:784px) {
  .navbar-links {
    display: none;
  }
}